//
.ImgVidPreviewItem {
    width: 3rem;
    height: 3rem;
    img,
    video {
        width: 100%;
        height: 100%;
        border-radius: 5px;
        object-fit: cover;
    }
}

// .
.ImgVidPreviewItem_delete {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
}
// ..
.ImgVidPreviewItem__icon-arrow {
    width: 1.1rem;
    height: 1.1rem;
    background-color: var(--danger);
}
