//
.ChatMess_has-felt{
    padding-bottom: 0.5rem;
}

// common
.ChatMess_common{
    display: flex;
    position: relative;
}

// message of current user
.ChatMess_user{
    justify-content: flex-end;
    // mess
    .ChatMess_content{
        background-color: var(--blue);
    }
    //
    .ChatMess__status{
        display: block;
        position: absolute;
        left: 100%;
        bottom: 0;
    }
    //
    .ChatMess__felt{
        left: auto;
        right: 5%;
    }
    //
    .ChatMess_more{
        left: auto;
        right: 105%;
        flex-direction: row;
    }
}

// mess
.ChatMess_mess{
    position: relative;
    max-width: 75%;
}

// content
.ChatMess_content{
    position: relative;
    margin-left: auto;
    width: fit-content;
    
    padding: 0 0.5rem;
    background-color: var(--md-bg-ccc);
    border-radius: 10px;
    white-space: pre-wrap;
}

// vid pic
.ChatMess_vid-pic{
    .VidPics_count{
        grid-template-rows: 6rem 6rem;
        .VidPics_count_5:nth-child(4)::after{
            font-size: 0.5rem;
        }
    }
}

// status for sending
.ChatMess__status{
    display: none;
}

// felt for a message
.ChatMess__felt{
    position: absolute;
    bottom: -5px;
    left: 5%;
    transform: translateY(50%);
}
.ChatMess__felt-row{
    display: flex;
    align-items: center;
}
.ChatMess__felt-elm{
    display: flex;
    justify-content: center;
    align-items: center;

    width: 1.25rem;
    height: 1.25rem;
    padding: 0.1rem;
    
    border-radius: 50%;
    line-height: 1rem;
    .IconsTypeLike{
        width: 1rem;
        height: 1rem;
    }
    margin-left: 0.1rem;
}
.ChatMess__felt-user{
    background-color: var(--blue-blur);
}
.ChatMess__felt-friend{
    background-color: var(--ccc-blur);
}

//
.ChatMess_more{
    display: flex;
    align-items: center;
    flex-direction: row-reverse;

    position: absolute;
    top: 50%;
    left: 105%;

    transform: translateY(-50%);
    z-index: 2;
}

//
.ChatMess__like{
    // position: relative;
    margin: 0 0.25rem;
}
