//
.VidPics_blur {
    .VidPics_blur-img {
        background-size: cover;
        background-repeat: no-repeat;
    }
}

//
.VidPics_current {
    position: absolute;
    left: 10%;
    bottom: 4rem;
    height: 80%;
    width: 80%;
}
.VidPics_current-contain {
    img,
    video {
        min-width: 50%;
        max-width: 100%;
        max-height: 100%;
    }
}

/* ------------------- ALL ------------------- */
.VidPics_all {
    position: absolute;
    left: 50%;
    bottom: 0;
}
.VidPics_all-contain {
    max-width: 20rem;
    overflow-x: auto;
    transform: translateX(-50%);
}

//
@media (max-width: 20rem) {
    .VidPics_all-contain {
        max-width: 100vw;
    }
}
