.FetchingDiv{
    display: flex;
    
    .FetchingDiv_item{
        margin: 0.5rem;
        width: 0.5rem;
        height: 0.5rem;
        border-radius: 50%;

        animation: fetching_div 1.5s infinite linear;
        
            &:nth-child(2){
                animation-delay: 0.5s;
            }
            &:nth-child(3){
                animation-delay: 1s;
            }
        }
}

@keyframes fetching_div{
    0% {background-color: var(--blue);}
    50% {background-color: transparent;}
    100% {background-color: transparent;}
}